@import url("https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css");
@import "./../Static/Fonts/manrope.scss";

* {
  font-family: "Manrope", Arial, sans-serif;
  font-weight: 400;
}

.container {
  max-width: 700px;
  margin-left: 110px;
  margin-right: auto;
  padding-right: 20px;

  &-mini {
    max-width: 160px;
    margin-left: auto;
    margin-right: 90px;
    padding-left: 20px;
  }
}

.transition {
  &_bg {
    transition: background-color .4s;
  }
  &_text {
    transition: color .4s;
  }
}
